  const [address, setAddress] = useState(false);
  const [emergency, setEmergency] = useState(false);
  const [bank, setBank] = useState(false);
  const [profileData, setProfileData] = useState({});
  const location = useLocation();
  const navigate = useNavigate();
  var employeeId = location.state;
  console.log(employeeId)
  const dispatch = useDispatch();
  const profile = useSelector(
    (state) => state.employeeprofile.Employeeprofile?.[0]
  );
  const sideprofile = useSelector(
    (state) => state.employeeprofile.Sideemployeprofile ?? {}
  );

  console.log(sideprofile);
  console.log(profile);

  useEffect(() => {
    dispatch(fetchProfileRequest(employeeId));
    dispatch(fetchSideProfileRequest(employeeId));
  }, [dispatch, employeeId]);
  useEffect(() => {
    setProfileData(profile);
  }, [profile]);

  const handleEditClick = () => {
    setEditable(true);
  };
  const handleAddressClick = () => {
    setAddress(true);
  };
  const handleEmergencyClick = () => {
    setEmergency(true);
  };
  const handleBankClick = () => {
    setBank(true);
  };
  const handleSaveClick = () => {
    // Perform the save functionality here, e.g., update the profile data on the server
    // After saving, set editable to false to deactivate all input fields
    setEditable(false);

    dispatch(updateProfileRequest(employeeId, profileData));
  };
  const handleAddressSave = () => {
    setAddress(false);
    dispatch(addAddressRequest(employeeId, profileData));
  };
  const handleEmergencySave = () => {
    setEmergency(false);
    dispatch(addEmergencyRequest(employeeId, profileData));
  };
  const handleBankSave = () => {
    setBank(false);
    dispatch(addBankRequest(employeeId, profileData));
  };
  const handleCancelClick = () => {
    // Discard any changes made while editing by reverting the data to the original profile data
    setProfileData(profile);
    // After canceling, set editable to false to deactivate all input fields
    setEditable(false);
    setAddress(false);
    setEmergency(false);
    setBank(false);
  };

  const handleInputChange = (e, field) => {
    // Update the data of the field being edited
    setProfileData((prevProfileData) => ({
      ...prevProfileData,
      [field]: e.target.value,
    }));
  };
const handleJob =()=>{
  
  navigate("../employejob", {state: employeeId})
}
if (!profile || Object.keys(profile).length === 0) {
  return <div>Loading...</div>; // Or any other loading state
}
